import {Component, OnDestroy, OnInit} from '@angular/core';
import {BehaviorSubject} from "rxjs";
import {FormControl, FormGroup, Validators} from "@angular/forms";
import {AppContextService} from "../../../../services/app-context.service";
import {HttpClient} from "@angular/common/http";
import {DatePipe} from "@angular/common";

@Component({
  selector: 'app-sms',
  templateUrl: './sms.component.html',
  styleUrls: ['./sms.component.css']
})
export class SmsComponent implements OnInit, OnDestroy {

    private subscriptions = [];
    public currentBalance = new BehaviorSubject(0);
    public smsPaymentGroup = new FormGroup({
	sumControl: new FormControl('20', [Validators.required, Validators.min(20)]),
	paymentType : new FormControl('PC'),
    });
  constructor(
      public appContext : AppContextService,
      public http : HttpClient,
      public datePipe : DatePipe) { }

  ngOnInit() {
  
  }
  
  ngOnDestroy(): void {
      this.subscriptions.forEach(sub => sub.unsubscribe());
  }
  onSubmitForm(){
      let sum = this.smsPaymentGroup.get('sumControl').value,
	  now = Date.now();
      this.subscriptions.push(this.http.post('https://money.yandex.ru/quickpay/confirm.xml',{}, {
          params : {
              receiver : '41001510819857', 
	      'quickpay-form' : 'donate',
	      targets : 'SMS',
	      paymentType : this.smsPaymentGroup.get('paymentType').value,
	      sum : sum,
	      label : `${this.appContext.appUser.uid}:${sum}:${now}`,
	      formcomment : `Оплата SMS трафика:${sum}:${ this.datePipe.transform(now,'yyyy-MM-dd')}`,
	      'short-dest' : `Оплата SMS трафика на сумму ${sum} : ${ this.datePipe.transform(now,'yyyy-MM-dd')}`,
	      comment : `${this.appContext.appUser.uid}:${sum}:${now}` ,
	      successURL : 'https://web-rtc-peer.herokuapp.com/application/main/sms'
	  }}).subscribe(res =>{
	     debugger;
      }));
  }
  
}
